<template>
    <main>
        <div class="weui-cells weui-cells_form quit">
            <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="numberfn">
                <div class="weui-cell__hd" style="width:1.7rem;">
                    退房订单号
                </div>
                <div class="weui-cell__bd">
                    <p v-if="!info.orderNum">请选择订单号</p>
                    {{info.orderNum}}
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd">
                    <label class="weui-label">退房房号</label>
                </div>
                <div class="weui-cell__bd">
                    {{info.roomNum}}
                </div>
            </div>
            <div class="weui-cell weui-cell_switch">
                <div class="weui-cell__bd">
                    <div class="Prompt">需开发票</div>
                </div>
                <div class="weui-cell__ft">
                    <label for="switchCP1" class="weui-switch-cp">
                        <input id="switchCP1" v-model="info.invoiceEnable" class="weui-switch-cp__input" type="checkbox">
                        <div class="weui-switch-cp__box"></div>
                    </label>
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd">
                    <label class="weui-label">发票抬头</label>
                </div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="text" v-model="info.invoiceHead" placeholder="请填写发票抬头">
                </div>
            </div>
            <div class="weui-cell weui-cell_select weui-cell_select-after mode" @click="invoicefn">
                <div class="weui-cell__hd" style="width:1.7rem;">
                    发票类目
                </div>
                <div class="weui-cell__bd">
                    <p v-if="!info.invoiceCategory">请选择发票类目</p>
                    {{info.invoiceCategory}}
                </div>
            </div>
            <div class="btn">
                <a href="javascript:;" class="weui-btn weui-btn_primary" @click="Submit">确定</a>
            </div>
            <Success v-if="isok" text="已预约"></Success>
        </div>
        <LOGO></LOGO>
    </main>
</template>

<script type='text/ecmascript-6'>
import LOGO from "@/components/Logo";
import Success from "@/components/Success";
import weui from "@/assets/js/weui.min";
import { QuitModular, common } from "@/assets/js/request.js";
import { emptytest } from "@/assets/js/filters.js";
export default {
  data() {
    return {
      hotelId: this.$store.state.hotelId,
      isok: false,
      checkInOrderdata: "",
      invoicedata: "",
      info: {
        orderNum: "",
        roomNum: "",
        invoiceEnable: 0,
        invoiceHead: "",
        invoiceCategory: ""
      }
    };
  },
  components: {
    LOGO,
    Success
  },
  methods: {
    //发票类目
    invoicefn() {
      var _this = this;
      weui.picker(_this.invoicedata, {
        onChange: function(result) {},
        onConfirm: function(result) {
          _this.info.invoiceCategory = result[0].label;
        }
      });
    },
    // 退房订单号
    numberfn() {
      var _this = this;
      weui.picker(_this.checkInOrderdata, {
        onChange: function(result) {},
        onConfirm: function(result) {
          _this.info.roomNum = "";
          _this.info.orderNum = result[0].label;
          result[0].value.forEach(item => {
            _this.info.roomNum += item.roomNum + ",";
          });
          _this.info.roomNum = _this.info.roomNum.slice(
            0,
            _this.info.roomNum.length - 1
          );
        }
      });
    },
    Submit() {
      if (!emptytest(this.info.orderNum)) {
        this.Toast("请选择订单号");
        return false;
      }
      if (!emptytest(this.info.roomNum)) {
        this.Toast("请输入退房房号");
        return false;
      }
      if (this.info.invoiceEnable == 1) {
        if (!emptytest(this.info.invoiceHead)) {
          this.Toast("请填写发票抬头");
          return false;
        }
        if (!emptytest(this.info.invoiceCategory)) {
          this.Toast("请选择发票类目");
          return false;
        }
      }
      var newdata = JSON.parse(JSON.stringify(this.info));
      if (this.info.invoiceEnable == 0) {
        newdata.invoiceEnable = 1;
      } else if (this.info.invoiceEnable == 1) {
        newdata.invoiceEnable = 0;
      }
      QuitModular.Quitrooms({
        Suffix: this.hotelId + "/checkOut",
        params: newdata,
        fn: data => {
          this.isok = true;
          var _this = this;
          setTimeout(() => {
            _this.isok = false;
            _this.$router.go(0);
          }, 2000);
        }
      });
    }
  },
  mounted() {
    QuitModular.getQuitrooms({
      Suffix: this.hotelId + "/checkInOrder",
      fn: data => {
        this.checkInOrderdata = [];
        data.records.forEach(item => {
          this.checkInOrderdata.push({
            label: item.orderNum,
            value: item.rooms
          });
        });
      }
    });
    common.invoice({
      Suffix: this.hotelId + "/invoice",
      fn: data => {
        this.invoicedata = [];
        data.records.forEach(item => {
          this.invoicedata.push({
            label: item.dictCnName,
            id: item.id
          });
        });
      }
    });
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>
main {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  padding-bottom: 1.3rem;
}

.quit {
  flex: 1;
  background: #f4f4f4;
}
</style>



